<template>
  <h2>Add new photo:</h2>

  <form>
    <input type="file" accept="image/jpeg/*" @change="UploadImage()"
    />
    <p class="info_paragraph">Select location on the map ...</p>
    <div id="map"></div>
    <p class="info_paragraph">or set it manually:</p>
    <p>latitude:</p>
    <input type="text" v-model="coords[0]" required>
    <p>longitude:</p>
    <input type="text" v-model="coords[1]" required>
    <p>
      <input type="submit" class="login" value="Save">
    </p>
  </form>

</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'
export default {

  data () {
    return {
      login: '',
      password: '',
      error: false,
      coords: []
    }
  },
  mounted() {
    this.$nextTick(function () {
        ymaps.ready( ()=> {
          const myMap = new ymaps.Map('map', {
            center: [53.893009, 27.567444],
            zoom: 10
          });
          myMap.events.add('click', (e) =>{
            if (!myMap.balloon.isOpen()) {
              this.coords = e.get('coords');
              myMap.balloon.open(this.coords, {
                contentHeader:'Photo was taken here!',
                contentBody:
                  '<p>Coordinates: ' + [
                    this.coords[0].toPrecision(6),
                    this.coords[1].toPrecision(6)
                  ]
              });
            }
            else {
              myMap.balloon.close();
            }
          })
        })
    })
  },
  methods: {
    UploadImage(){
      const file = document.querySelector('input[type=file]').files[0]
      const reader = new FileReader()

      let rawImg;
      reader.onloadend = () => {
        rawImg = reader.result;
        console.log(rawImg);
      }
      reader.readAsDataURL(file);
      console.log(file)

      const user = JSON.stringify({ base64Image: file, lat: 50.0, lng: 60 })
      const customConfig = {
        headers: {
          'Content-Type': 'application/json'
        }
      }
      axios.post('/api/image', user, customConfig).then(response => {
        const temp = response.data
      }).catch((error) => {
        this.error = true
        console.log(error.response.status)
      })


    }
  }
}
</script>

<style>
#map{
  width: 600px;
  height: 400px;
  margin-left: 35%;
}
.info_paragraph{
  font-size: 25px;
}
</style>
